* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: letter-content-font;
  src: url(./fonts/shesek-regular-fm.otf);
}

@font-face {
  font-family: letter-title-font;
  src: url(./fonts/avigul-bold-fm.otf);
}

body {
  height: 100vh;
  overflow: hidden;
  background: radial-gradient(#ffbb00, #cd3000);
  font-family: Helvetica, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  width: 375px;
  height: 597px;
  perspective: 800px;
  margin-bottom: 95px;
}

.flip > .card {
  transform: rotateY(180deg);
}

.card {
  height: 100%;
  width: 100%;
  position: relative;
  transition: transform 1500ms;
  transform-style: preserve-3d;
}

.front,
.back {
  height: 100%;
  width: 100%;
  border-radius: 31px;
  box-shadow: 0 0 5px 2px rgba(50, 50, 50, 0.25);
  position: absolute;
  backface-visibility: hidden;
}

.front {
  display: flex;
  justify-content: center;
  background-image: url("./assets/front_code.jpg");
  background-size: contain;
}

.back {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  transform: rotateY(180deg);
  display: flex;
  background-image: url("./assets/back_code.jpg");
  background-size: contain;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  direction: rtl;
  padding: 0 15px;
}

#back-footer {
  position: absolute;
  font-size: 40px;
  color: white;
  width: 50%;
  bottom: 30px;
  right: 20px;
  display: flex;
  align-items: center;
  gap: 10%;
}

#letter {
  position: relative;
  color: white;
  bottom: 25px;
}

#letter-title {
  font-family: letter-title-font;
}

#letter-content {
  font-family: letter-content-font;
}

.text-xs {
  font-size: 16px;
}
.text-sm {
  font-size: 18px;
}
.text-md {
  font-size: 21px;
}
.text-lg {
  font-size: 24px;
}

.text-xl {
  font-size: 27px;
}

#logo {
  width: 304.5px;
  height: 304.5px;
  border-radius: 50%;
  position: relative;
  top: 19px;
  animation: glowing 2000ms infinite;
}

.back-icon:focus,
.back-icon:active {
  opacity: 60%;
}

.pressable:hover {
  cursor: pointer;
}

#phone {
  position: absolute;
  bottom: 68px;
  right: 45px;
  color: #cd3000;
  opacity: 40%;
  letter-spacing: 3px;
  transform: rotateZ(-4.5deg);
}

#photo {
  position: absolute;
  text-align: center;
  max-width: 90%;
  max-height: 80%;
  z-index: 1;
  border-radius: 10px;
  outline: 5px solid white;
  top: -100%;
}

.fly-in {
  top: 20px !important;
  animation: fly-in-animation 0.5s ease-in-out;
}

.fly-out {
  animation: fly-out-animation 0.5s ease-in-out;
}

@keyframes fly-in-animation {
  0% {
    transform: translateY(-200%);
    top: -100%;
    opacity: 0%;
  }
  100% {
    opacity: 100%;
    top: 20px;
  }
}

@keyframes fly-out-animation {
  0% {
    opacity: 100%;
    top: 20px;
  }
  100% {
    opacity: 0%;
    top: -100%;
  }
}

.press {
  color: white;
  font-size: 35px;
  position: absolute;
  opacity: 80%;
  z-index: 1;
}

@keyframes glowing {
  0% {
    box-shadow: 0 0 -10px #fff200;
  }
  40% {
    box-shadow: 0 0 25px #fff200;
  }
  60% {
    box-shadow: 0 0 25px #fff200;
  }
  100% {
    box-shadow: 0 0 -10px #fff200;
  }
}
